import axios from 'axios';

import { stores } from 'store';

class Api {
  generateHeader = () => {
    this.headers = {
      Accept: 'application/json',
      'Content-Type': 'application/json',
    };
  }

  customFetch = (url, method, body = {}, showLoading = true) => {
    this.generateHeader();

    const options = {
      url: url,
      method,
      headers: this.headers,
    };

    if (method === 'GET') {
      options.params = body;
    } else if (method === 'POST') {
      options.data = JSON.stringify(body);
    }
    stores.forEach(store => {
      store.dispatch({ type: "STATE_FETCHING", data: { isLoading: showLoading } });
    }); 
    return new Promise((resolve, reject) => {
      axios.request(options)
        .then((res) => {
          stores.forEach(store => {
            store.dispatch({ type: "STATE_FETCHING", data: { isLoading: false } });
          });
          return resolve(res);
        })
        .catch((error) => {
          stores.forEach(store => {
            store.dispatch({ type: "STATE_FETCHING", data: { isLoading: false } });
          });
          return reject(error);
        });
    });
  }



  upload = (url, body) => {
    let options = {
      url: url,
      method:"POST",
      data:body,
      headers: {
        Accept: 'application/json',
        'Content-Type': 'multipart/form-data',
      }
    };

    return new Promise((resolve, reject) => {
      axios.request(options)
        .then((res) => {
          return resolve(res);
        })
        .catch((error) => {
          return reject(error);
        });
    });
  }

  get = (url, showLoading = true) => this.customFetch(url, 'GET', {}, showLoading);

  post = (url, body, showLoading = true) => this.customFetch(url, 'POST', body, showLoading);

  put = (url, body, showLoading = true) => this.customFetch(url, 'PUT', body, showLoading);

  delete = (url, body, showLoading = true) => this.customFetch(url, 'DELETE', body, showLoading);
}

export default new Api();
