
const SET_DIRTY = "SET_DIRTY";
const STATE_FETCHING = "STATE_FETCHING";

function templateReducer(state = {}, action) {
  switch (action.type) {
    case SET_DIRTY:
      return setDirty(state, action);
    case STATE_FETCHING:
      return { ...state, ...action.data };
    default:
      return state;
  }
}


function setDirty(state, action) {
  return {
    ...state, [action.componentCode]: true
  };
}

export default templateReducer;
