
export const isEquivalent = (a, b) => {
  if (typeof a === "function" || typeof b === "function") {
    return false
  } else if (typeof a !== "object" || typeof b !== "object") {
    return a === b
  }

  // Create arrays of property names
  let aProps = a ? Object.getOwnPropertyNames(a) : [];
  let bProps = b ? Object.getOwnPropertyNames(b) : [];

  // If number of properties is different,
  // objects are not equivalent
  if (aProps.length !== bProps.length) {
    return false;
  }

  for (var i = 0; i < aProps.length; i++) {
    let propName = aProps[i];

    // If values of same property are not equal,
    // objects are not equivalent
    if (propName !== "key" && !isEquivalent(a[propName], b[propName])) {
      return false;
    }
  }

  // If we made it this far, objects
  // are considered equivalent
  return true;
}

export const diff = (obj1, obj2) => {

  // Make sure an object to compare is provided
  if (!obj2 || Object.prototype.toString.call(obj2) !== '[object Object]') {
    return obj1;
  }

  //
  // Variables
  //

  let diffs = {};
  let key;


  //
  // Methods
  //

  /**
   * Check if two arrays are equal
   * @param  {Array}   arr1 The first array
   * @param  {Array}   arr2 The second array
   * @return {Boolean}      If true, both arrays are equal
   */
  let arraysMatch = function (arr1, arr2) {

    // Check if the arrays are the same length
    if (arr1.length !== arr2.length) return false;

    // Check if all items exist and are in the same order
    for (var i = 0; i < arr1.length; i++) {
      if (arr1[i] !== arr2[i]) return false;
    }

    // Otherwise, return true
    return true;

  };

  /**
   * Compare two items and push non-matches to object
   * @param  {*}      item1 The first item
   * @param  {*}      item2 The second item
   * @param  {String} key   The key in our object
   */
  const compare = function (item1, item2, key) {

    // Get the object type
    let type1 = Object.prototype.toString.call(item1);
    let type2 = Object.prototype.toString.call(item2);

    // If type2 is undefined it has been removed
    if (type2 === '[object Undefined]') {
      diffs[key] = null;
      return;
    }

    // If items are different types
    if (type1 !== type2) {
      diffs[key] = item2;
      return;
    }

    // If an object, compare recursively
    if (type1 === '[object Object]') {
      let objDiff = diff(item1, item2);
      if (Object.keys(objDiff).length > 0) {
        diffs[key] = objDiff;
      }
      return;
    }

    // If an array, compare
    if (type1 === '[object Array]') {
      if (!arraysMatch(item1, item2)) {
        diffs[key] = item2;
      }
      return;
    }

    // Else if it's a function, convert to a string and compare
    // Otherwise, just compare
    if (type1 === '[object Function]') {
      if (item1.toString() !== item2.toString()) {
        diffs[key] = item2;
      }
    } else {
      if (item1 !== item2) {
        diffs[key] = item2;
      }
    }

  };


  //
  // Compare our objects
  //

  // Loop through the first object
  for (key in obj1) {
    if (obj1.hasOwnProperty(key)) {
      compare(obj1[key], obj2[key], key);
    }
  }

  // Loop through the second object and find missing items
  for (key in obj2) {
    if (obj2.hasOwnProperty(key)) {
      if (!obj1[key] && obj1[key] !== obj2[key]) {
        diffs[key] = obj2[key];
      }
    }
  }

  // Return the object of differences
  return diffs;

};

export const isValidDate = (d) => {
  return d instanceof Date && !isNaN(d);
}

export const rtlLanguage = ['ar']

export const reorder = (list, startIndex, endIndex) => {
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);
  return result;
};

export const removeFromList = (list, index) => {
  const [removed] = list.splice(index, 1);
  return [removed, list];
};

export const removeValue = (list, value) => {
  const index = list.indexOf(value);
  if (index > -1) { // only splice array when item is found
    list.splice(index, 1); // 2nd parameter means remove one item only
    return list
  }
  return list
};

export const addToList = (list, index, element) => {
  list.splice(index, 0, element);
  return list;
};


export const nextGroupId = (groups) => {
  if (groups && groups.length) {
    return groups.map((group) => parseInt(group.code.replace("G", ""))).sort(function (a, b) { return a - b })[groups.length - 1] + 1
  }
  return 1;
}


export const nextQuestionId = (state, groups) => {
  if (groups.length) {
    let questions = []
    groups.forEach(group => {
      let groupObj = state[group.code]
      if (groupObj.children) {
        groupObj.children.forEach(question => {
          questions.push(parseInt(question.code.replace("Q", "")))
        })
      }
    });
    if (questions.length) {
      return questions.sort(function (a, b) { return a - b })[questions.length - 1] + 1
    }
  }
  return 1;
}

export const nextId = (elements) => {
  if (elements.length) {
    let arrayOfIntCodes = elements
      .filter(el => el.type != "other")
      .map(el => el.code.replace(/^\D+/g, ''))
      .filter(el => el.length > 0)
    if (arrayOfIntCodes.length) {
      let intCodes = arrayOfIntCodes
        .map(el => parseInt(el))
        .sort(function (a, b) { return a - b })
      if (intCodes) {
        return intCodes[intCodes.length - 1] + 1
      }
    }
  }
  return 1;
}

export const dragInfo = (index, source, destination) => {
  let sourceGroupCode = ""
  for (var i = 0; i < source && i < index.length; i++) {
    if (index[i].startsWith("G")) {
      sourceGroupCode = index[i]
    }
  }
  let parentIndex = index.indexOf(sourceGroupCode)
  index = reorder(
    index,
    source,
    destination
  );

  let destGroupCode = ""
  for (var i = 0; i < destination && i < index.length; i++) {
    if (index[i].startsWith("G")) {
      destGroupCode = index[i]
    }
  }
  let destIndex = index.indexOf(destGroupCode)

  let onlyGroups = index.filter((elem) => elem.startsWith("G"))


  return {
    sameGroup: sourceGroupCode == destGroupCode,
    relativeChildSourceIndex: source - parentIndex - 1,
    relativeChildDestIndex: destination - destIndex - 1,
    sourceGroupCode: sourceGroupCode,
    destGroupCode: destGroupCode,
    sourceGroupIndex: onlyGroups.indexOf(sourceGroupCode),
    destGroupIndex: onlyGroups.indexOf(destGroupCode)
  }
};
export const newQuestionsDragInfo = (index, destination) => {
  let destGroupCode = ""
  for (let i = 0; i < destination && i < index.length; i++) {
    if (index[i].startsWith("G")) {
      destGroupCode = index[i]
    }
  }
  let destIndex = index.indexOf(destGroupCode)

  let onlyGroups = index.filter((elem) => elem.startsWith("G"))


  return {
    relativeChildDestIndex: destination - destIndex - 1,
    destGroupCode: destGroupCode,
    destGroupIndex: onlyGroups.indexOf(destGroupCode)
  }
};

export const isHtml = (string) => {
  return /<\/?[a-z][\s\S]*>/i.test(string)
}
