import Api from "common/api";
import { defaultSurveyTheme } from "constants";


export async function GetData(setState, setError, showLoading) {
  setError(false);
  let search = window.location.search;
  let params = new URLSearchParams(search);
  let url = "http://" +
    params.get("url") +
    "/survey/designer_input/" +
    params.get("sid");
  try {
    const response = await Api.get(url, showLoading);
    processResponse(response, setState)
    return response.data;
  } catch (err) {
    setError({
      errorType: "click",
      message: err.message
    });
  }
}


export const uploadResourceFile = (selectedFile) => {
  let search = window.location.search;
  let params = new URLSearchParams(search);
  const formData = new FormData();
  formData.append('file', selectedFile);
  let url = "http://" +
    params.get("url") + "/survey/" +
    params.get("sid") + "/resource/upload"
  return Api.upload(url, formData)
}

export const buildResourceUrl = (fileName) => {
  let search = window.location.search;
  let params = new URLSearchParams(search);
  return "http://" +
    params.get("url") + "/survey/" +
    params.get("sid") + "/resource/" + fileName
}

export const buildResourcStreameUrl = (fileName) => {
  let search = window.location.search;
  let params = new URLSearchParams(search);
  return "http://" +
    params.get("url") + "/survey/" +
    params.get("sid") + "/resource/stream/" + fileName
}



export async function SetData(state, setState, setError, showLoading) {
  setError(false);
  let search = window.location.search;
  let params = new URLSearchParams(search);
  let url = "http://" +
    params.get("url") +
    "/api/edit_components/" +
    params.get("sid");
  try {
    const response = await Api.post(url, state, showLoading);
    processResponse(response, setState, state.langInfo)

  } catch (err) {
    setError({
      errorType: "click",
      message: err.message
    });
    GetData(setState, setError, showLoading);
  }
}

const processResponse = (response, setState, langInfo) => {
  let state = response.data.state;

  if (!state.Survey.theme) {
    state.Survey.theme = defaultSurveyTheme
  }
  state.langInfo = {
    languagesList: response.data.languages,
    mainLang: response.data.languages[0].code,
    lang: langInfo?.lang || response.data.languages[0].code
  }
  state.componentIndex = response.data.componentIndexList
  setState(state)
}

export function ResetData(setState, setError) {
  SetData(sample_survey, setState, setError, true);
}


export const toPreview = (lang) => {
  let search = window.location.search;
  let params = new URLSearchParams(search);
  let url = params.get("url")
  let sid = params.get("sid")
  window.open(`http://${window.location.host}/?url=${url}&sid=${sid}&lang=${lang}&navigation_mode=ALL_IN_ONE`, '_blank', 'noopener,noreferrer');
};


