import { configureStore } from '@reduxjs/toolkit'
import frankieStateEngine from "surveyEngine/stateMachine";
import designState from "design/state/designState";
import { dataSaver } from "design/state/saveDebounce"
import templateReducer from "reducers/templateReducer";

export const runStore = configureStore({ reducer: { template: templateReducer, runState: frankieStateEngine } });
export const designStore = configureStore({
    reducer: { template: templateReducer, designState },
    middleware: (getDefaultMiddleware) =>
        getDefaultMiddleware().concat(dataSaver)
});

export const stores = [runStore, designStore]