export const instructionByCode = (component, code) => component.instructionList
    ? component.instructionList.find(el => el.code === code)
    : undefined


export const stripTags = (string) => {
    return string ? string.replace(/<[^>]*>?/gm, '')
        .replace("\n", "")
        .replace("&nbsp;", "") : string;
}

export const isQuestion = (code) => /^Q[a-z0-9_]+$/.test(code)
export const isGroup = (code) => /^G[a-z0-9_]+$/.test(code)




export const lastIndexInArray = (array, func)=>{

    if(!array){
        return -1
    }
    let index = array.length -1
    for ( ; index >= 0; index--) {
        if (func(array[index])) {
            return index
        }
    }
    return -1
}