function NewComponentsPanel() {
  let draggableIndex = 0
  let groupsdraggableIndex = 0
  return (
    <>
      <Droppable droppableId="new-questions" type="questions" isDropDisabled>
        {(provided) => (
          <div className={styles.leftContent} ref={provided.innerRef}
            {...provided.droppableProps}>
            {questions.map((item, index) => (
              <div className={styles.leftPanelGroupItem} index={index} key={index}>
                <div className={styles.groupTitle}>{item.name}</div>
                <div className={styles.leftItems}>
                  {item.items.map((question, index) => {
                    draggableIndex++
                    return (
                      <div key={`draggable-${index}`}>
                        <Draggable
                          draggableId={question.type}
                          index={draggableIndex}
                        >
                          {(provided, snapshot) => {
                            return (
                              <div
                                ref={provided.innerRef}
                                snapshot={snapshot}
                                {...provided.draggableProps}
                                {...provided.dragHandleProps}
                              >
                                <NewComponentsItem
                                  item={question}
                                  index={index}
                                />
                              </div>
                            );
                          }}
                        </Draggable>
                      </div>
                    );
                  })}
                </div>
              </div>
            ))}
            {provided.placeholder}
          </div>
        )}
      </Droppable>
      <Droppable droppableId="new-groups" type="groups" isDropDisabled>
        {(provided) => (
          <div className={styles.leftContent} ref={provided.innerRef}
            {...provided.droppableProps}>
            {groups.map((item, index) => (
              <div className={styles.leftPanelGroupItem} index={index} key={index}>
                <div className={styles.groupTitle}>{item.name}</div>
                <div className={styles.leftItems}>
                  {item.items.map((question, index) => {
                    groupsdraggableIndex++
                    return (

                      <Draggable
                        key={question.type}
                        draggableId={question.type}
                        index={groupsdraggableIndex}
                      >
                        {(provided, snapshot) => {
                          return (
                            <div
                              ref={provided.innerRef}
                              snapshot={snapshot}
                              {...provided.draggableProps}
                              {...provided.dragHandleProps}
                            >
                              <NewComponentsItem
                                item={question}
                                index={index}
                              />
                            </div>
                          );
                        }}
                      </Draggable>

                    );
                  })}
                </div>
              </div>
            ))}
            {provided.placeholder}
          </div>
        )}
      </Droppable>
    </>
  );
}

